import Link from 'next/link';
import { request } from 'graphql-request';

const Post = props => (
  <div>
    <h1>{props.route.routeName}</h1>
  </div>
);

Post.getInitialProps = async function(context) {
  const { routeName } = context.query;
  query = `{
      route(routeName: ${routeName}){
          sailings {
              vessel
          }
      }
  }`;
  const res = await request('https://server-dubymzzcjd.now.sh/graphql', query);
  console.log(`Route info fetched. Sailings: ${res.sailings.length}`);
  return res;
};

export default Post;



